.welcome-text{
    display: flex;
    flex-direction: row;
    /* gap: 1em; */
    background-color: gainsboro;
    padding-top: 9px;
    padding-bottom: 10px;
    padding-left: 20px;
    padding-right: 20px;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 116px;
}

.col-1 h1{
    color: black;
    text-align: center;
    font-family: Open Sans;
    font-size: 26px;
    font-style: italic;
    font-weight: 700;
    line-height: normal;;
    height: 17px;
}

.browse-btn{
    display: inline-block;
    background-color:rgba(255, 184, 0, 1);
    color: black;
    font-family: Open Sans;
    font-weight: 700;
    font-size: 20px;
    padding: 8px 30px;
    margin: 30px 0;
    border-radius: 30px;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}
.browse-btn:hover{
    background-color: #E36E02;
    transform: scale(0.98);
}

