.right-arrow {
    margin-left: 1px;
    padding-left: 0px;
    margin-right: 0px;
    padding-right: 0px;
    transform: rotate(180deg);
}

.book-rec-frame{
    position: relative;
    display: flex;
    flex-direction: row;
    /* gap: 1em; */
    background-color: #D9D9D9;
    padding-left: 20px;
    padding-right: 20px;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 576px;
    height: 297px;
    flex-shrink: 0;
}

