
.footbar {
    display: grid;
    grid-template-columns: 30% 40% 30%;
    /*grid-column-gap: 15px;*/
    align-items: center;
    background-color: rgba(30, 30, 30, 1);
    height: auto;
    padding: 2px;
}


.foot-elements {
    margin-left: auto;
    display: flex;
    gap: 12%;
    list-style: none;
    justify-content: space-evenly;
    align-items: center;
}

.foot-elements2 {
    margin-left: auto;
    display: flex;
    list-style: none;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    font-family: "Inter";
    font-weight: 700;
    line-height: normal;
    padding: 15px 0px 15px 0px;
    margin: 0px;
}

.foot-elements li {
    font-size: 14px;
    font-family: "Inter";
    font-weight: 700;
    line-height: normal;
    padding: 15px 0px 15px 0px;
    margin: 0px;
}


.foot-elements li a {
    text-decoration: none;
}

.foot-elements li svg {
    /*transform: translateY(12px);*/
    justify-content: space-around;
    padding: 3px;
}

.foot-elements h3 {
    font-size: 10px;
    font-family: "Inter";
    font-weight: 700;
    line-height: normal;
    padding: 15px 0px 15px 0px;
    margin-right: 0px;
}
