.category_container{
    color: black;
    font-family: Open Sans;
    font-size: 18px;
    font-style: normal;
    font-weight: 100;
    line-height: normal;
    background-color: #D9D9D9;
    margin-left: 0px;
    display: flex;
    height: 60px;
    width: 1300px;
    flex-direction: row;
    padding: 20px 50px;
    justify-content: center;
    align-items: center;
    gap: 1px;
    flex-shrink: 0px;
}

.book-boxes {
    max-width: 1300px;
    display: grid;
    /* grid-template-columns: repeat(auto-fit, minmax(12em, 3fr)); */
    grid-row-gap: 1em;
    /* gap: 1em; */
    /* auto */
    /* grid-template-columns: repeat(auto-fit, minmax(250px, 1fr)); */
    grid-template-columns: repeat(3, minmax(0px, 1fr));
    @media only screen and (max-width: 1250px)  {
      grid-template-columns: repeat(2, minmax(0px, 1fr));
    }

    @media only screen and (max-width: 850px)  {
      grid-template-columns: repeat(1, minmax(0px, 1fr));
    }
    
}
  
  .book-box {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 1em;
    background-color: gainsboro;
    max-width: 540px;
    width: 400px;
    padding: 1em;
  }

  .book-box:hover {
    transform: scale(1.1);
  }
  
  .book-box.has-transition {
    transition: transform 150ms ease-in-out;
  }
  
  .book-image-and-info {
    display: flex;
    flex-direction: row;
    gap: 0.5em;
  }
  
  .book-image {
    /* width: fit-content; */
    height: 200px;
  }

.cart-book-sideimage{
  height: 200px;
}

.read-btn{
    padding: 0 2px;
    /*background-color: #E8D000;*/
    color: black;
    font-size: 40px;
    font-weight: bold;
    border-radius: 25px;
    position: relative;
    top: -220px;
    right: -54px;
    width: 2rem;

}
  
  .book-info {
    display: flex;
    flex-direction: column;
    gap: 0.25em;
    color: black;
    font-family: Open Sans;
}

.book-author{
    font-family: Open Sans;
    font-style: italic;
    font-size: 13px;
    color: black;
}

.info-buy{
    display: flex;
    flex-direction: column;
    gap: 20px;
    color: black;
    font-family: Open Sans;
}
  
  .book-title {
    font-weight: 600;
    font-size: 17px;
    color: black;
    font-family: Open Sans;
  }
  
  .book-price {
    /* font-style: italic; */
    font-weight: 700;
    color: black;
    font-family: Open Sans;
  }
  
  .buy-now-button {
    /* background-color: gold;
    padding: 0.25em 1em;
    align-self: end; */
    font-family: Open Sans;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    border: 0;
    display: flex;
    flex-direction: column;
    width: 120px;
    height: 32px;
    padding: 12px 10px;
    justify-content: center;
    align-items: center;
    color: black;
    gap: 10px;
    flex-shrink: 0;
    border-radius: 30px;
    background: #FF7A00;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    cursor: pointer;
  }

  .buy-now-button:hover{
      background-color: #E36E02;
      transform: scale(0.98);
  }