.navbar {
  display: grid;
  grid-template-columns: 20% 30% 60%;
  grid-column-gap: 30px;
  align-items: center;
  background-color: rgba(30, 30, 30, 1);
  height: auto;
  padding: 5px;
  padding-right: 60px;
}

.navbar li{
  display: inline-block;
  margin-right: 20px;
}

.nav-elements {
  margin-left: auto;
  display: flex;
  gap: 12%;
  list-style: none;
  justify-content: space-evenly;
  align-items: center;
}

.nav-elements li {
  font-size: 25px;
  font-family: "Inter";
  font-weight: 700;
  line-height: normal;
  padding: 15px 0px 15px 0px;
  margin: 0px;
}


.nav-elements li a {
  text-decoration: none;
}


.nav-elements li svg {
  transform: translateY(12px);

}

.nav-elements button {
  font-size: 23px;
  font-family: "Inter";
  font-weight: 600;
  line-height: normal;
  background-color: rgba(30,30,30,1);
  color: white;
  padding: 5px 5px 5px 5px;
  margin: 20px;
  border: none;
  cursor: pointer;
}

.nav-elements button:hover{
  text-decoration:underline;
}

.nav-elements button a {
  text-decoration: none;
}


.nav-elements button svg {
  transform: translateY(12px);

}

.cat-dropdown{
  display:block;
  position: relative;
}

.cat-dropdown ul {
  background-color: rgba(30,30,30,1);
  display: none;
  text-align: center;
  position: absolute;
  z-index: 5;
}

.cat-dropdown li {
  font-size: 18px;
  font-family: "Open Sans";
  font-weight: 500;
  padding: 5px;
  margin: 10px;
  cursor: pointer;
}

.cat-dropdown li:hover {
  background-color: dimgrey;
  border-radius: 0.5pc;
}


#dropdown-hover{
  position: absolute;
  display:none;
  width: 100%
}


.cat-dropdown:hover #dropdown-hover{
  display:block;

}

#dropdown-hover li{
  display: block;

}

.header-drop-and-cart{
  display: flex;
  flex-direction: row;
  margin: 12px;
  padding: 5px;
}

.search-container{
  background-color: rgba(217, 217, 217, 1);
  position: relative;
  /* border-radius: 30px; */
  padding: 3px 50px 3px 10px;
  justify-content: center;
  display: inline-block;
  width: 120%;
}
