#search-bar{
  background-color: transparent;
  outline: none;
  height: 35px;
  font-size: 15px;
  border: 0;
  width: 100%;
  color: black;
}

#search-bar:focus{
  color: black;
}

.search-button {
  position: absolute;
  font-size: 25px;
  right: 4px;
  top: 4px;
  background-color: rgba(217, 217, 217, 1);
  /* border-radius: 50%; */
  border: 0;
  color: #FFF;
  width: 35px;
  height: 35px;
  outline: 0;
  cursor: pointer;
}

.badge {
  padding: 0 2px;
  background-color: #E8D000;
  color: black;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 25px;
  position: relative;
  top: -35px;
  right: -14px;
  width: 1.2rem;
  text-decoration-line: none;
}
