.cart-table {
    display: grid;
    grid-template-columns: max-content minmax(15em, 25em) repeat(3, max-content);
    row-gap: 1em;
    width: fit-content;
    margin-top: 25px !important;
    background-color: #333;
    font-weight: 600;
}

.quantity-input{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 3px;
}

.quantity-btn-modifier{
    padding: 8px;
    width: 35px;
    font-size: 1rem;
    
    background: #f3f3f3;
    color: #888;
    border: 0 solid #dbdbdb;
    text-align: center;
    cursor: pointer;
}

.quantity-btn-modifier:hover{
    background: #dadada;
    color: #555555;
}



.quantity-input-minus{
    /* border-radius: 3px 0 0 3px; */
}

.quantity-input-plus{
    /* border-radius: 0 3px 3px 0px; */
}

.quantity-input-modifier{
    width: 40px;
    padding: 8px;
    font-size: 1rem;
    border: 0;
    border-top: 0 solid #dbdbdb;
    border-bottom: 0 solid #dbdbdb;
    text-align: center;
    color: black;
}

.product-quantity {
    width: 100px;
}


.product-quantity input {
    text-align: center;
    color: black
}

.product-quantity:before {
    content: 'x';
}

ul {
    display: contents;
}

ul > li {
    display: contents;
}

ul > li:last-child {
    margin-bottom: 0.5em;
}

.table-heading {
    background-color: #333;
    color: white;
    font-weight: bold;
}

.table-heading > * {
    background-color: #333;
    padding: 0.5em;
    font-weight: bold;
}

.heading-book {
    grid-column: 2 / 3;
    font-weight: bold;
}

.heading-price {
    grid-column: 3 / 4;
    text-align: right;
    font-weight: bold;
}

.heading-quantity {
    grid-column: 4 / 5;
    text-align: right;
    font-weight: bold;
}

.heading-subtotal {
    text-align: right;
    grid-column: -2 / -1;
}

.cart-book-image {
    padding: 0 1em;
}

.cart-book-image > * {
    margin-left: auto;
    margin-right: 0;
}

.cart2 img {
    display: block;
    width: 100px;
    height: auto;
}

.cart-book-price {
    display: flex;
    align-items: center;
    padding: 5px;
}

.cart-book-quantity {
    display: flex;
    align-items: center;
    padding: 5px;
}

.cart-book-subtotal {
    display: flex;
    align-items: center;
    padding: 5px;
}

.cart-items-alert {
    margin-top: 25px; 
    color: black;
    text-align: left;
    margin-left: 13em;
}

/* Row separators in the table */

.line-sep {
    display: block;
    height: 2px;
    background-color: gray;
    grid-column: 1 / -1;
}

/* Increment/decrement buttons */

.icon-button {
    border: none;
    cursor: pointer;
}

.inc-button {
    font-size: 1.125rem;
    color: var(--primary-color);
    margin-right: 0.25em;
}

.inc-button:hover {
    color: var(--primary-color-dark);
}

.dec-button {
    font-size: 1.125rem;
    color: #ccc;
}

.dec-button:hover {
    color: #aaa;
}

/* Chevron buttons */

.dec-arrow-button,
.inc-arrow-button {
    font-size: 1rem;
    color: var(--primary-color);
}

.dec-arrow-button:hover,
.inc-arrow-button:hover {
    color: var(--primary-color-dark);
}

input[type="number"] {
    width: 4em;
}

select {
    background-color: var(--primary-color);
    color: white;
    border: 2px solid var(--primary-color-dark);
    border-radius: 3px;
}
.row-line {
    border-bottom: 1px solid black;
}

.cart-buttons {
    background-color: black;
}