.cart-empty {
  color: black;
  margin-top: 0.5em;
  margin-bottom: 7.5em;
}

.cart-total {
  color: black;
  font-size: 1.5em;
  margin-top: 50px;
  margin-bottom: 1em;
  border-top: 1px solid #ccc;
}
.checkout-button {
  background-color: #f0c040;
  border: none;
  padding: 0.5em;
  border-radius: 0.5em;
  color: black;
  font-weight: 700;
  cursor: pointer;
}
.cart-button-container {
  display: flex;
  gap: 5;
}

.clear-cart-button {
  background-color: #f0c040;
  border: none;
  padding: 0.5em;
  border-radius: 0.5em;
  color: black;
  font-weight: 700;
  cursor: pointer;
}
.cart-container {
  margin-bottom: 1em;
}